// footer.scss

.footer-wrapper {
    display: flex;
    flex-direction: column;
    @include padding-left-right;
    background-color: $color__brand-red;
    color: #fff;
    text-align: center;
    font-family: $font__regular;
    border-bottom: 5px solid #fff;
    font-size: 0.8125rem;
    padding-top: 1em;
    padding-bottom: 1em;

    h1 {
        color: #fff;
        font-size: 1.063rem;
        margin-bottom: 0;
        font-family: $font__bold;

        @include respond-to('tablet') {
            font-size: 1.2em;
        }
    }

    .contact-section {

        .contact-wrapper {
            .contact-numbers {
                padding-top: 1em;
            }
        }
        .hours {
            margin-top: 1em;

            @include respond-to('tablet') {
                margin-top: 0;
            }
        }

        .resident-button {
            display: none;

            button {
                text-transform: uppercase;
                margin-top: 2em;
                padding: 1em;
                font-family: $font__regular;
            }

        }

        @include respond-to('tablet') {
            max-width: 500px;
            flex: 1;
            // margin: auto;
            text-align: left;
            display: flex;
            justify-content: space-between;

            .resident-button {
                display: block;
            }
        }
    }

    .icons-section {
        margin-top: 2em;
        .social {
            h1 {
                margin-bottom: .5em;
            }

            a {
                padding: 5px;
            }

            svg {
                max-width: 42px;
            }

            @include respond-to('tablet') {
                display: flex;
                justify-content: flex-end;
                align-items: center;
            }
        }

        .brand {
            margin-top: 1.5em;

            svg {
                margin-left: 5px;
                margin-right: 5px;
            }

        }

        .translate {
            margin-top: 1.5em;

        }

        .mm4 {
            margin-top: 1.5em;
            margin-bottom: 1.5em;
            font-size: 0.75rem;

            a {
                text-decoration: none;
                color: #fff;
                font-family: $font__light;
            }

        }

        @include respond-to('tablet') {
            flex: 1;
            margin: auto;
            text-align: right;
        }
    }

    @include respond-to('tablet') {
        border-top: 5px solid #fff;
        flex-direction: row;
        font-size: 1.1em;
        line-height: 1.25;
    }
} // site-footer